@import url(https://cdn.jsdelivr.net/npm/bulma@0.9.3/css/bulma.min.css);
/* Setting up CSS variables in the root */
:root {
  --main-black: #000;
  --main-green: #1aa169;
  --main-green-gray: #457d70;
  --main-light-plum: #924577;
  --main-white: #ffffff;
}
  
/* Base reset */
* {
  margin: 0;
  padding: 0;
}

/* Box-sizing */
*,
*::before,
*::after {
  box-sizing: inherit;
}

/* Element styling */ 
html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

body {
  color: #000;
  color: var(--main-black);
  font-family: 'Poppins', sans-serif;
  font-size: 1.125rem;
  font-weight: 400;
  line-height: 1.4;
}

h1 {
  font-size: 3.125rem;
}

h2 {
  font-size: 2.625rem;
}

h1, h2 {
  font-family: 'Raleway', sans-serif;
  font-weight: 700;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

p {
  display: block;
  -webkit-margin-before: 1em;
          margin-block-start: 1em;
  -webkit-margin-after: 1em;
          margin-block-end: 1em;
  -webkit-margin-start: 0px;
          margin-inline-start: 0px;
  -webkit-margin-end: 0px;
          margin-inline-end: 0px;
}
  
img {
  display: block;
}

/* MEDIA QUERIES */
/****************************************************/ 
/* For large PHONES, TABLETS and larger devices - min-width 425px */
/****************************************************/ 
@media only screen and (min-width: 425px) {
  /* Element styling */
  h2 {
      font-size: 3rem;
  }
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Navigation Bar styling */
#navbar {
    background: var(--main-green);
    box-shadow: 0 0.125rem 0 rgba(71, 71, 71, 0.6);
    left: 0;
    top: 0;
    width: 100%;
    z-index: 2;
}
  
#navbar h1 {
    display: none;
    margin-left: 0.625rem;
}

#nav-list {
    display: block;
}

.nav-link {
    color: var(--main-white);
    display: block;
    font-size: 1.125rem;
    padding: 1.25rem;
}

.nav-link:hover {
    background: var(--main-green-gray);
    color: var(--main-white);
    height: 100%;
}

.active {
    background: var(--main-light-plum);
    height: 100%;
}

/* MEDIA QUERIES */
/****************************************************/ 
/* For large PHONES, TABLETS and larger devices - min-width 425px */
/****************************************************/ 
@media only screen and (min-width: 425px) {
    /* Header section styling */
    #navbar {
        display: flex;
        justify-content: flex-end;
    }
    #nav-list {
        display: flex;
        margin-right: 1.25rem;
    }
    #nav-list li a {
        font-size: 1.375rem;
    }
}
  
/***********************************************************/ 
/* For larger TABLETS and bigger devices - min-width 768px */
/***********************************************************/ 
@media only screen and (min-width: 768px) {
    /* Header section styling */
    #navbar {
        position: fixed;
    }
}

/****************************************************/ 
/* For LAPTOPS and larger devices - min-width 992px */
/****************************************************/ 
@media only screen and (min-width: 992px) {
    /* Header section styling */
    #navbar {
        justify-content: space-between; 
    }
    #navbar h1 {
        color: var(--main-black);
        display: block;
        font-size: 3.125rem;
        font-weight: 600;
    }
}
  
/* Subtitle section styling */
#subtitle-text h3 {
    background-color: var(--main-light-plum);
    bottom: 4.375rem;
    color: var(--main-white);
    display: none;
    font-size: 1.5625rem;
    font-style: italic;
    font-weight: 100;
    margin: 0 auto;
    opacity: 70%;
    position: relative;
    text-align: center;
    top: 4.375rem;
    z-index: 0;
}

/***********************************************************/ 
/* For larger TABLETS and bigger devices - min-width 768px */
/***********************************************************/ 
@media only screen and (min-width: 768px) {
    #subtitle-text h3 {
        display: block;
    }
}
/* About Me section styling */
#about-me-section {
    align-items: center;
    background: var(--main-white);
    display: flex;
    flex-direction: column;
    height: 100vh;                            /* Takes up 100% of the viewport height */
    justify-content: center;
    width: 100%;
}

#about-me-section h2 {
    font-size: 3rem;
}

#about-me-section p {
    background: var(--main-white);
    font-size: 1.5625rem;
    font-style: italic;
    font-weight: 200;
    padding: 1.5rem;
    text-align: justify;
    width: 90%;
}

.photo {
    border: 0.125rem solid var(--main-black);
    border-radius: 6.2rem;
    display: none;
    opacity: 90%;
    position: relative;
    /* top: 2.5rem; */
    width: 180px;
}

#contact-links {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    max-width: 980px;
    margin-top: 2.5rem;
    width: 100%;
}
  
#contact-links a:hover {
    color: var(--main-white);
}
  
.contact-details {
    font-size: 1.5rem;
    /* text-shadow: 2px 2px 1px #747474; */
    transition: transform 0.3s ease-out;
}
  
.btn {
    border-radius: 2px;
    color: var(--main-black);
    display: inline-block;
    padding: 0.625rem 1.25rem;
    text-decoration: none;
}
  
.btn:hover {
    background: #333333;
    border-radius: 2rem;
}

/* MEDIA QUERIES */
/****************************************************/ 
/* For large PHONES, TABLETS and larger devices - min-width 425px */
/****************************************************/ 
@media only screen and (min-width: 425px) {
    /* About Me section styling */
    #about-me-section p {
        font-size: 1.875rem;
    }
}
  
/***********************************************************/ 
/* For larger TABLETS and bigger devices - min-width 768px */
/***********************************************************/ 
@media only screen and (min-width: 768px) {
    /* About Me section styling */
    #about-me-section {
        background: url(/Portfolio-React/static/media/bg1.e7a416c6.jpg) no-repeat center center fixed;
        background-size: cover;
    }
    #about-me-section p {
        margin: 0 auto;
    }
    .photo {
        display: block;
    }

    .photo:hover {
        opacity: 85%;
    }
}
  
/****************************************************/ 
/* For LAPTOPS and larger devices - min-width 992px */
/****************************************************/ 
@media only screen and (min-width: 992px) {
    /* About Me section styling */
    #about-me-section p {
        width: 60%;
    }
}
  
/*************************************/  
/* For DESKTOP PC - min-width 1200px */
/*************************************/ 
@media only screen and (min-width: 1200px) {
    /* About Me section styling */
    #about-me-section p {
        width: 50%;
    }
}
/* Project Work section styling */
#projects {
    background-image: linear-gradient(62deg, #3a3d40 0%, #181719 100%);
    padding: 7rem 1.25rem;
    text-align: center;
}

.projects-section-header {
    bottom: 0.625rem;
    color: var(--main-white);
    font-size: 2.625rem;
    margin: 0 auto;
    padding-bottom: 30px;
    position: relative;
}

#projects h3 {
    border-bottom: 0.125rem solid var(--main-white);
    font-style: italic;
    margin-bottom: 1.875rem;
    max-width: 640px;
}

main > .projects-container {
    margin-bottom: 2.5rem;
}

.projects-container {
    max-width: 1280px;
    margin: 0 auto;
    margin-bottom: 3.75rem;
    width: 100%;
}

.project-work {
    width: 100%;
}

.project-image {
    object-fit: cover;
    width: 100%;
}

.project-image:hover {
    opacity: 90%;
}

.project-work-paragraph {
    background: var(--main-green);
    box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
    color: var(--main-white);
    font-size: 1.25rem;
    margin: 0 auto;
    padding: 1rem 0rem;
    width: 100%;
}

.project-work-paragraph:hover, .project-repository:hover {
    background: var(--main-light-plum);
    color: var(--main-white);
}

.project-repository {
    background: #177e53;
    color: var(--main-white);
    display: flex;
    font-size: 1.25rem;
    justify-content: center;
    padding: 0.4rem 0rem;
    width: 100%;
}
/* Footer section styling */
footer {
    background: var(--main-green);
    border-top: 2px solid var(--main-green-gray);
    color: var(--main-white);
    display: flex;
    font-weight: 300;
    justify-content: center;
    padding: 1.25rem;
}
  
footer p a {
    color: var(--main-white);
}
  
footer p a:hover {
    color: var(--main-light-plum);
}
